#exito {
    margin: 0;
    padding: 0;
   
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    font-family: 'Raleway';
}

#exito h1{
    text-align: center;
    font-size: 26px;
    padding-top: 25px;
    font-family: 'Nunito', sans-serif !important;
}

#exito p.p13{
    text-align: center;
    color: rgb(139, 139, 139);
    font-size: 15px;
    padding-top: 7px;
    font-family: 'Nunito', sans-serif !important;
}

#exito .botonn{
    margin: 200px auto;
    display: block;
    text-decoration: none;
    padding: 10px 60px;
    background-color: rgb(219, 187, 7);
    font-size: 18px;
    color: rgb(0, 0, 0);
    border-radius: 5px;
    font-family: 'Nunito', sans-serif !important;
    border: none;
    cursor: pointer;
}

#exito .pay{
    margin-top: 30px;
    padding: 0px 20px;
}

#exito .payment-title {
    width: 100%;
    text-align: center;
}

#exito .form-container .field-container:first-of-type {
    grid-area: name;
}

#exito .form-container .field-container:nth-of-type(2) {
    grid-area: number;
}

#exito .form-container .field-container:nth-of-type(3) {
    grid-area: expiration;
}

#exito .form-container .field-container:nth-of-type(4) {
    grid-area: security;
}

#exito .field-container input {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

#exito .field-container {
    position: relative;
}

#exito .form-container {
    float: right;
    display: grid;
    grid-column-gap: 10px;
    grid-template-columns: auto auto;
    grid-template-rows: 90px 90px 90px;
    grid-template-areas:
        "name name"
        "number number"
        "expiration security";
    max-width: 306px;
    padding: 20px 0px;
    color: #707070;
    margin-top: -112px;
}

#exito label {
    padding-bottom: 5px;
    font-size: 13px;
}

#exito input {
    margin-top: 3px;
    padding: 15px;
    font-size: 16px;
    width: 100%;
    border-radius: 3px;
    border: 1px solid #dcdcdc;
}

#exito .ccicon {
    height: 38px;
    position: absolute;
    right: 6px;
    top: calc(50% - 17px);
    width: 60px;
}

/* CREDIT CARD IMAGE STYLING */
#exito .preload * {
    -webkit-transition: none !important;
    -moz-transition: none !important;
    -ms-transition: none !important;
    -o-transition: none !important;
}

#exito .container {
    width: 100%;
    max-width: 332px;
    max-height: 245px;
    height: 54vw;
    padding: 23px 20px;
}

#exito .grupo {
    /* max-width: 400px; */
    float: right;
    width: 275px;
    /* margin-bottom: 10px; */
    margin: -273px 32px;
}

#exito .grupoo {
    /* max-width: 400px; */
    float: right;
    width: 275px;
    /* margin-bottom: 10px; */
    margin: -184px 32px;
}

#exito #ccsingle {
    position: absolute;
    right: 15px;
    top: 20px;
}

#exito #ccsingle svg {
    width: 100px;
    max-height: 60px;
}

#exito .creditcard svg#cardfront,
#exito .creditcard svg#cardback {
    width: 100%;
    -webkit-box-shadow: 1px 5px 6px 0px black;
    box-shadow: 1px 5px 6px 0px black;
    border-radius: 22px;
}

#generatecard{
    display: none;
}

/* CHANGEABLE CARD ELEMENTS */
#exito .creditcard .lightcolor,
#exito .creditcard .darkcolor {
    -webkit-transition: fill .5s;
    transition: fill .5s;
}

