}

#exito .creditcard .reddark {
    fill: #d32f2f;
}

#exito .creditcard .purple {
    fill: #ab47bc;
}

#exito .creditcard .purpledark {
    fill: #7b1fa2;
}

#exito .creditcard .cyan {
    fill: #26c6da;
}

#exito .creditcard .cyandark {
    fill: #0097a7;
}

#exito .creditcard .green {
    fill: #66bb6a;
}

#exito .creditcard .greendark {
    fill: #388e3c;
}

#exito .creditcard .lime {
    fill: #3d70ff;
}

#exito .creditcard .limedark {
    fill: #002bfb;
}

#exito .creditcard .yellow {
    fill: #ffeb3b;
}

#exito .creditcard .yellowdark {
    fill: #f9a825;
}

#exito .creditcard .orange {
    fill: #ff9800;
}

#exito .creditcard .orangedark {
    fill: #ef6c00;
}

#exito .creditcard .grey {
    fill: #bdbdbd;
}

#exito .creditcard .greydark {
    fill: #616161;
}

/* FRONT OF CARD */
#svgname {
    text-transform: uppercase;
}

#cardfront .st2 {
    fill: #FFFFFF;
}

#cardfront .st3 {
    font-family: 'Source Code Pro', monospace;
    font-weight: 600;
}

#cardfront .st4 {
    font-size: 54.7817px;
}

#cardfront .st5 {
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
}

#cardfront .st6 {
    font-size: 33.1112px;
}

#cardfront .st7 {
    opacity: 0.6;
    fill: #FFFFFF;
}

#cardfront .st8 {
    font-size: 24px;
}

#cardfront .st9 {
    font-size: 36.5498px;
}

#cardfront .st10 {
    font-family: 'Source Code Pro', monospace;
    font-weight: 300;
}

#cardfront .st11 {
    font-size: 16.1716px;
}

#cardfront .st12 {
    fill: #4C4C4C;
}

/* BACK OF CARD */
#cardback .st0 {
    fill: none;
    stroke: #0F0F0F;
    stroke-miterlimit: 10;
}

#cardback .st2 {
    fill: #111111;
}

#cardback .st3 {
    fill: #F2F2F2;
}

#cardback .st4 {
    fill: #D8D2DB;
}

#cardback .st5 {
    fill: #C4C4C4;
}

#cardback .st6 {
    font-family: 'Source Code Pro', monospace;
    font-weight: 400;
}

#cardback .st7 {
    font-size: 27px;
}

#cardback .st8 {
    opacity: 0.6;
}

#cardback .st9 {
    fill: #FFFFFF;
}

#cardback .st10 {
    font-size: 24px;
}

#cardback .st11 {
    fill: #EAEAEA;
}

#cardback .st12 {
    font-family: 'Rock Salt', cursive;
}

#cardback .st13 {
    font-size: 37.769px;
}

/* FLIP ANIMATION */
#exito .container {
    perspective: 1000px;
}

#exito .creditcard {
    width: 100%;
    max-width: 400px;
    -webkit-transform-style: preserve-3d;
    transform-style: preserve-3d;
    transition: -webkit-transform 0.6s;
    -webkit-transition: -webkit-transform 0.6s;
    transition: transform 0.6s;
    transition: transform 0.6s, -webkit-transform 0.6s;
    cursor: pointer;
}

#exito .creditcard .front,
#exito .creditcard .back {
    position: absolute;
    width: 100%;
    max-width: 400px;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-font-smoothing: antialiased;
    color: #47525d;
}

#exito .creditcard .back {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

#exito .creditcard.flipped {
    -webkit-transform: rotateY(180deg);
    transform: rotateY(180deg);
}

